import { Component, OnInit } from '@angular/core';
import {FormBuilder, Validators,FormGroup,FormControl} from "@angular/forms";
@Component({
  selector: 'app-adminhome',
  templateUrl: './adminhome.component.html',
  styleUrls: ['./adminhome.component.css']
})
export class AdminhomeComponent implements OnInit {

  constructor(public forms:FormBuilder) { }
public inputgroup;
abc(){
  console.log(this.inputgroup.value);
  console.log('form working');
}
  ngOnInit() {
    this.inputgroup=this.forms.group({
      username:new FormControl('', Validators.compose([
        Validators.required,Validators.minLength(4),Validators.maxLength(20),
        Validators.pattern('^[a-zA-Z_.+-]+$')
      ])),
      lastname:new FormControl('', Validators.compose([
        Validators.required,Validators.minLength(4),Validators.maxLength(20),
        Validators.pattern('^[a-zA-Z_.+-]+$')
      ])),
      fathername:new FormControl('', Validators.compose([
        Validators.required,Validators.minLength(4),Validators.maxLength(20),
        Validators.pattern('^[a-zA-Z_.+-]+$')
      ])),
      mothername:new FormControl('', Validators.compose([
        Validators.required,Validators.minLength(4),Validators.maxLength(20),
        Validators.pattern('^[a-zA-Z_.+-]+$')
      ])),
     Day:[null,[Validators.required]],
     Month:[null,[Validators.required]],
     Year:[null,[Validators.required]],
     Education:[null,[Validators.required]],
     PassOut:[null,[Validators.required]],
     Course:[null,[Validators.required]],
     Cast:[null,[Validators.required]],
     Email:[null,[Validators.required,Validators.minLength(2-20),Validators.email]],
     Phone:new FormControl('', Validators.compose([
      Validators.required,Validators.minLength(10),Validators.maxLength(10),
      Validators.pattern('^[0-9.+-]+$')
    ])),
     Address:new FormControl('', Validators.compose([
      Validators.required,Validators.minLength(4),Validators.maxLength(40),
      Validators.pattern('^[a-zA-Z0-9/:_.+-]+$')
    ])),
    });
  }}

